<template>
    <div class="product_container">
        <div class="image_container">
            <img :src="require(`../assets/images/${imgUrl}`)" alt="">
        </div>
        <div class="information_container">
            <h1>{{ name }}</h1>
            <h5>商品編號：{{ productId }}</h5>
            <h5>材質：{{ material }}</h5>
            <h3><span>NTD </span>{{ price }}</h3>
            <hr>
            <h5 class="des_title">商品敘述：</h5>
            <div class="word_space">
                <p> {{ description }} </p>
            </div>
        </div>
    </div>
    <div class="item_amount">
        <input type="number" v-model.number="number"> &nbsp;&nbsp;
        <button type="button" class="btn btn-warning" :class="[{inCart: productInCart[productId]}]"
        @click="pushIn_shoppingCart(productId); addCart(productsData)">加入購物車</button>
    </div>
</template>
<script>

export default {
    props: {
        'productId': Number,
        'name': String,
        'price': Number,
        'description': String,
        'material': String,
        'imgUrl': String,
        'quantity': Number
    },
  data(){
      return{
          productsData: this.$store.state.productsData,
          number: 0,
          productInCart: {},
      }
  },
  // 有取到productId但無法順利將商品資料導到購物車，且加入購物車的數字計算有誤
  methods: {
    pushIn_shoppingCart: function (productId) {
      this.productInCart[productId] = this.productInCart[productId] ? false : true
      this.$emit('buying', this.productInCart)
      console.log(this.productInCart)
    },
    addCart: function(product){
      // 判斷存貨
      if(product.quantity - this.number < 0){
        alert('存貨不足')
        return;
      }
      this.$store.commit('addCart', {
        product: product,
        number: this.number
      })
      alert('已加入購物車')
    }
  },
  computed:{

  }
  }

</script>
<style scoped>
    .product_container{
        margin-top: 170px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    .image_container{
        min-width: 360px;
        min-height: 360px;
        width: 450px;
        height: 450px;
    }

    .image_container img{
        width: 100%;
        height: 100%;
    }

    .information_container {
        min-width: 450px;
        width: 700px; 
        margin: 0px 30px;
    }

    .information_container h1{
        font-size: 30px;
        font-weight: bold;
    }

    @media screen and ( max-width: 1228px ) {
        .information_container h1{
            margin-top: 15px;
        }
    }

    .information_container h5{
        margin: 15px 0px;
    }

    .information_container h3{
        font-weight: bold;
        color: red;
    }

    .word_space{
        padding: 0px 20px;
    }

    .item_amount{
        display: flex;
        justify-content: right;
        margin-right: 13%;
        font-size: 18px;
        margin-top: 10px;
        margin-bottom: 130px;
    }

    @media screen and ( max-width: 1228px ) {
        .item_amount{
            justify-content: center;
            margin-right: 0;
        }
    }

    .item_amount input{
        display: block;
    }

    .item_amount button{
        display: block;
    }
</style>
